
import BirdyService from '../BirdyService'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'


export default {
  data () {
    return {
      birdyService: new BirdyService(this),
      content: {},
      target: {}
    }
  },
  created () {
    this.getData()
    this.pageInit()
    let url = location.href.split('#')[0]
    this.birdyService.get(`/api/wechat/config?url=${url}`).then((resp) => {
      this.config = resp
      this.$wechat.config({
        debug: false, // ,apialertpclogpc
        appId: resp.appId, // 
        timestamp: resp.timestamp, // 
        nonceStr: resp.nonceStr, // 
        signature: resp.signature,// 1
        jsApiList: ['onMenuShareAppMessage'] // JSJS2
      })

      this.$wechat.onMenuShareAppMessage({
        title: `${this.target.name}`, // 
        desc: '', // 
        link: `http://loan.bailongsheng.cn/webapp/confirm/${this.$route.params.id}`, // JS
        imgUrl: 'http://loan.bailongsheng.cn/upload/base/loan_logo.png', // 
        type: '', // ,musicvideolinklink
        dataUrl: '', // typemusicvideo
        success: function () {
            // 
        },
        cancel: function () {
            // 
        }
      })
    }).catch((resp) => {
      console.log(resp)
    }).finally(() => {

    })
  },
  computed: {
    formatDate () {
      return dateFormat(new Date(this.target.created_at * 1000), 'YYYY-MM-DD')
    }
  },
  methods: {
    pageInit () {
      this.birdyService.get(`/api/content/1?access-token=abc123_`).then((resp) => {
        this.content = resp.content
      }).catch((resp) => {
        console.log(resp)
      }).finally(() => {

      })
    },
    getData () {
      this.birdyService.get(`/api/loan/view/${this.$route.params.id}?access-token=abc123_`).then((resp) => {
        this.target = resp
        // document.getElementsByTagName('title')[0].innerText = `${this.target.name}`
      }).catch((resp) => {

      }).finally(() => {

      })
    },
    confirm () {
      this.target.loan_status = 2
      this.birdyService.update(`/api/loan/update/${this.$route.params.id}?access-token=abc123_`).then((resp) => {
        window.location.href = this.content.link
      }).catch((resp) => {

      }).finally(() => {

      })
    }
  },
  components: {
    XButton, Box
  }
}



